body {
    margin: 0;
    padding: 0;
    font-family: 'Lato', sans-serif;
    background-color: lightgrey;
}
#main {
    background: rgb(98, 142, 238); 
    width: 50vw;
    min-height: 100px;
    margin: 20px auto;
    padding: 20px;
    border-radius: 25px;
}
#info {
    font-size: 1.2vw;
    font-weight: 500;
    color: #00226b;
    margin: 20px auto;
    display: block;
    width: 80%;
    
}
.header {
    text-align: center;
    font-size: 3.5vw;
    color: white;
}
.singleItem {
    font-size: 1.2vw;
    overflow-wrap: break-word;
    letter-spacing: 0.05vw;
    color: white;
    background-color: rgb(0, 34, 230);
    padding: 30px;
    margin: 20px auto;
    width: 90%;
    box-sizing: border-box;
    border-radius: 12px;
}



button, textarea {
    display: block;
    margin: 5px auto;
}
textarea {
    border-radius: 20px;
    resize: none;
    min-width: 80%;
    max-width: 80%;
    height: 49px;
    font-size: 1vw;
    padding: 15px;
}
button {
    cursor: pointer;
    border: 5px solid darkgray;
    border-radius: 20px;
    font-size: 1.5vw;
    padding: 15px;
}
button:active {
    background-color: darkgray;
}